import { Injectable, Output, EventEmitter } from '@angular/core';
import { WebApiManager } from '../shared/services/webApiManager.service';
import { AppSettings } from '../../app/appSettings.setting';
import { Observable } from 'rxjs/Rx';
import { Subject } from 'rxjs/Subject';
import { AuthService } from '../shared/services/auth.service';

@Injectable()
export class AdminService {

 adminDataCollection: Observable<any> = null;

  constructor(private webApiService: WebApiManager, public authService: AuthService) {
  }


  public static AdminUrl = AppSettings.BaseAPIUrl + 'admin/';

  getOpportunityData() {
    this.adminDataCollection = this.webApiService.Get(AdminService.AdminUrl + 'Dashboard');
    return this.adminDataCollection;
  }

  SaveOpportunity(opportunity: any) {
    return this.webApiService.Post(AdminService.AdminUrl + 'BulkAddUpdate', opportunity);
  }

  SubmitOpportunity(opportunity: any) {
    return this.webApiService.Post(AdminService.AdminUrl + 'Submit', opportunity);
  }

  getLookUpData() {
    return this.webApiService.Get(AppSettings.BaseAPIUrl + 'LookUpData');
  }
}



