import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { RouterModule, Routes } from '@angular/router';
// ag-grid
import { AgGridModule } from 'ag-grid-angular';

import { AdminHomeComponent } from './components/home/admin.home.component';
import { AdminService } from './admin.service';
import { AdminRouting } from './admin.routing';
import { WorkflowMappingUserAdminComponent } from './components/workflow-mapping-user.admin/workflow-mapping-user.admin.component';
import { WorkflowMappingAdminComponent } from './components/workflow-mapping.admin/workflow-mapping.admin.component';
import { LookupTypeAdminComponent } from './components/lookup-type.admin/lookup-type.admin.component';
import { LookupDataAdminComponent } from './components/lookup-data.admin/lookup-data.admin.component';

@NgModule({
  imports: [
    //BrowserModule,
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    RouterModule,
    ReactiveFormsModule,
    Ng4LoadingSpinnerModule.forRoot(),
    AdminRouting
  ],
  declarations: [AdminHomeComponent, WorkflowMappingUserAdminComponent, WorkflowMappingAdminComponent, LookupTypeAdminComponent, LookupDataAdminComponent],
  exports: [],
  entryComponents: [AdminHomeComponent],
  providers: [AdminService]

})
export class AdminModule { }



