import { Component, OnInit } from '@angular/core';
import {
  ReactiveFormsModule,
  FormsModule,
  FormGroup,
  FormControl,
  Validators,
  FormBuilder
} from '@angular/forms';

@Component({
  selector: 'app-lookup-data.admin',
  templateUrl: './lookup-data.admin.component.html',
  styleUrls: ['./lookup-data.admin.component.css']
})
export class LookupDataAdminComponent implements OnInit {
  lookupdataform: FormGroup;
  value: FormControl;
  lookUpType: FormControl;
  isActive: FormControl;
  createdDate: FormControl;
  lookUpTypeId: FormControl;
  constructor() { }

  ngOnInit() {
    this.createFormControls();
    this.createForm();
  }

  createFormControls() {
    this.value = new FormControl('', Validators.required);
    this.lookUpType = new FormControl('', Validators.required);
    this.isActive = new FormControl(true, Validators.required);
    this.createdDate = new FormControl('', Validators.required);
    this.lookUpTypeId = new FormControl('', Validators.required);
  }

  createForm() {
    this.lookupdataform = new FormGroup({
      value: this.value,
      lookUpType: this.lookUpType,
      isActive: this.isActive,
      createdDate: this.createdDate,
      lookUpTypeId: this.lookUpTypeId
    });
  }

  get Value() { return this.lookupdataform.get('value').value; }

  get LookUpType() { return this.lookupdataform.get('lookUpType').value; }

  get IsActive() { return this.lookupdataform.get('isActive').value; }

  get CreatedDate() { return this.lookupdataform.get('createdDate').value; }

  get LookUpTypeId() { return this.lookupdataform.get('lookUpTypeId').value; }
}



