import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminHomeComponent } from './components/home/admin.home.component';
import { AuthGuard } from '../shared/services/auth.guard.service';
import { WorkflowMappingUserAdminComponent } from './components/workflow-mapping-user.admin/workflow-mapping-user.admin.component';
import { WorkflowMappingAdminComponent } from './components/workflow-mapping.admin/workflow-mapping.admin.component';
import { LookupTypeAdminComponent } from './components/lookup-type.admin/lookup-type.admin.component';
import { LookupDataAdminComponent } from './components/lookup-data.admin/lookup-data.admin.component';


const adminRoutes: Routes = [
  {
    path: '',
    component: AdminHomeComponent,
    canActivate: [AuthGuard],
    children: [

      {
        path: '',
        children: [
          { path: 'admin', component: AdminHomeComponent, canActivate: [AuthGuard] },
          { path: 'userworkflow', component: WorkflowMappingUserAdminComponent, canActivate: [AuthGuard] },
          { path: 'lookuptype', component: LookupTypeAdminComponent, canActivate: [AuthGuard] },
          { path: 'workflowmapping', component: WorkflowMappingAdminComponent, canActivate: [AuthGuard] },
          { path: 'lookupdata', component: LookupDataAdminComponent, canActivate: [AuthGuard] }
        ]
      }
    ]
  }

];

export const AdminRouting: ModuleWithProviders = RouterModule.forChild(adminRoutes);



